<template>
    <div>
      <el-card class="box-card">
        <span>粉丝总数</span>
        <!-- <div>追踪患者</div> -->
        <el-button style="float: right; padding: 3px 0" type="text"
          ><i class="el-icon-more"></i>
        </el-button>
        <div id="charts1" style="width: 100%; height: 90px"></div>
      </el-card>
    </div>
  </template>
   
  <script>
  import * as echarts from "echarts";
   
  export default {
    name: "HealPatients",
    data() {
      return {};
    },
    mounted() {
      let myChart1 = echarts.init(document.getElementById("charts1"));
      myChart1.setOption({
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            // ['product', '2015', '2016', '2017'],
            ["1", 55],
            ["2", 56],
            ["3", 66],
            ["4", 73],
            ["5", 83],
            ["6", 103],
            ["7", 108],
          ],
        },
        xAxis: { type: "category" },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [{ type: "bar" }],
      });
    },
  };
  </script>
   
  <style  scoped>
  span {
    font-size: 15px;
  }
  .text {
    font-size: 12px;
  }
   
  .item {
    margin-bottom: 50px;
  }
   
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }
  </style>