<template>
    <div>
      <el-card class="box-card">
        <span>一个占位的表</span>
        <el-button style="float: right; padding: 3px 0" type="text"
          ><i class="el-icon-more"></i>
        </el-button>
        <div id="charts3" style="width: 100%; height: 90px"></div>
      </el-card>
    </div>
  </template>
   
  <script>
  import * as echarts from "echarts";
   
  export default {
    name: "HealPatients",
    data() {
      return {};
    },
    mounted() {
      let myChart3 = echarts.init(document.getElementById("charts3"));
      myChart3.setOption({
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [6, 12, 10, 4, 12, 13, 10],
            type: "line",
            smooth: true,
          },
        ],
      });
    },
  };
  </script>
   
  <style scoped>
  span {
    font-size: 15px;
  }
  .text {
    font-size: 12px;
  }
   
  .item {
    margin-bottom: 50px;
  }
   
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }
  </style>